.glyphicon {
    padding-right: 10px;
}

.Menu {
    overflow: auto;

    height: calc(100% - 34px);
}

.Menu__root {
    margin: 20px;
    padding: 0;
}

.Menu__item {
    display: block;
}

.Menu__item__text {
    word-break: break-all;
}

.Menu__item--active > a {
    cursor: default;

    color: #000;
}

.Menu__item + .Menu__item {
    margin-top: 10px;
}

.Menu__subMenu {
    margin: 10px 0 0;
    padding: 0 0 0 20px;
}

.Menu__item--subMenu > .Menu__subMenu {
    display: none;
}

.Menu__item--subMenu--open > .Menu__subMenu {
    display: block;
}



